import SecurityService from '@/services/nucleus/SecurityService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let AclTableMixin = {
  mixins: [DisabledClassMixin, LoadingMixin, ListLayoutMixin],
  props: {
    defaultAclTableFilter: {
      class: String,
      default: null
    },
    defaultAclTableOrderBy: {
      type: String,
      default: null
    },
    disabled: {
      type: String,
      default: null
    },
    userGroupId: {
      type: String,
      default: null
    },
    userId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      aclTableData: [],
      aclTableFilterTimer: null,
      aclTableFilter: this.defaultAclTableFilter,
      aclTableOrderBy: this.defaultAclTableOrderBy,
      aclTableContainer: 'loading-container',
      aclTableBusy: false,
      aclTableTotalRows: 0,
      aclTableCurrentPage: 1,
      aclTablePerPage: 10
    }
  },
  computed: {
    aclTableNoDataText: function () {
      return this.aclTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    aclTableTotalPages: function () {
      return Math.ceil(this.aclTableTotalRows / this.aclTablePerPage)
    },
    aclTablePagingInfo: function () {
      return this.pagingInfo(this.aclTableCurrentPage, this.aclTablePerPage, this.aclTableTotalRows)
    }
  },
  methods: {
    loadAclTableData (loadingContainerId) {
      let self = this
      this.aclTableBusy = true
      this.aclTableContainer = loadingContainerId || this.aclTableContainer
      this.startLoading(this.aclTableContainer)
      SecurityService.userGroupAcls({
        pageIndex: this.aclTableCurrentPage - 1,
        pageSize: this.aclTablePerPage,
        filter: this.aclTableFilter,
        orderByField: this.aclTableOrderBy,
        userGroupId: this.userGroupId,
        userId: this.userId,
        disabled: this.disabled
      }).then(function (response) {
        self.stopLoading(self.aclTableContainer)
        self.aclTableBusy = false
        self.aclTableTotalRows = response.data.totalRows
        self.aclTableData = response.data.aclList || []
      }).catch(function (error) {
        self.stopLoading(self.aclTableContainer)
        self.aclTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshAclTable () {
      this.loadAclTableData()
    },
    filterAclTable (filter) {
      let self = this
      if (this.aclTableFilter !== filter) {
        clearTimeout(this.aclTableFilterTimer)
        this.aclTableFilterTimer = setTimeout(() => {
          self.aclTableFilter = filter
          self.loadAclTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.aclTableFilterTimer)
      }
    },
    orderAclTable (orderBy, active) {
      this.aclTableOrderBy = (active ? '' : '-') + orderBy
      this.loadAclTableData()
    }
  },
  watch: {
    aclTableCurrentPage: function () {
      this.refreshAclTable()
    },
    aclTablePerPage: function () {
      this.refreshAclTable()
    }
  }
}

export default AclTableMixin
