<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="reportTableData" :noDataText="reportTableNoDataText" :sst="true" search @search="filterReportTable" @sort="orderReportTable" @selected="selectRow">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="loadReportTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="reportTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ reportTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="reportTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="reportId">
            {{ this.$t('ID') }}
          </vs-th>
          <vs-th sort-key="reportName">
            {{ this.$t('REPORT_NAME') }}
          </vs-th>
          <vs-th sort-key="categoryName">
            {{ this.$t('CATEGORY') }}
          </vs-th>
          <vs-th sort-key="typeDescription">
            {{ this.$t('TYPE') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].reportId">
              {{ data[index].reportId | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].reportName">
              {{ data[index].reportName | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].categoryName">
              {{ data[index].categoryName | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].typeDescription">
              {{ data[index].typeDescription | placeholder('-') }}
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="reportTableTotalPages" v-model="reportTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import ReportDefinitionTableMixin from '@/mixins/report/ReportDefinitionTableMixin'

export default {
  name: 'report-popup',
  mixins: [PopupMixin, ReportDefinitionTableMixin],
  mounted: function () {
    this.loadReportTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
